import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';

import { ISurvey } from '../../../../../../../src/app/shared/interfaces/result.interface';

@Component({
  selector: 'ac-result-element',
  templateUrl: './result-element.component.html',
  styleUrls: ['./result-element.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ResultElementComponent {
  @Input() element: ISurvey;
  @Output() onAction: EventEmitter<boolean> = new EventEmitter<boolean>();
  defaultImg2: string = 'data:image/jpeg;base64,';

  surveyAction() {
    this.onAction.emit(true);
  }
}
